import { randomUser } from './randomuser.js';

/**
 *
 * @param {String} baseUrl
 * @param {Object} [user]
 */
export default function buildUserTokenUrl( baseUrl, user = randomUser() ) {
	const tokenUrl = `${ baseUrl }?` + Object.keys( user )
		.filter( key => user[ key ] )
		.map( key => `user.${ key }=${ user[ key ] }` )
		.join( '&' );

	return tokenUrl;
}
