import uid from '@ckeditor/ckeditor5-utils/src/uid.js';
import axios from 'axios';

export function randomUserFromAPI() {
	const randomUserData = randomUser();
	return axios.get( 'https://randomuser.me/api/' )
		.then( ( { data } ) => {
			const userData = data.results[ 0 ];

			return {
				id: uid(),
				email: userData.email,
				name: randomUserData.name
			};
		} )
		.catch( () => {
			// If someday the API will be dead for a minute.
			return {
				id: uid(),
				email: 'j.smith@example.com',
				name: 'John Smith'
			};
		} );
}

export function randomUser() {
	const firstNames = [ 'Bo', 'Jo', 'Moe', 'Mex', 'Tex', 'Hex', 'Brick', 'Em', 'Plate', 'Zee', 'DJ', 'CJ', 'AJ' ];

	const lastNames = [ 'King', 'Egli', 'Zwart', 'Principe', 'Siddiqui', 'Ehlers', 'Coltrane', 'Grimes', 'Cavallaro',
		'Croce', 'Haddox', 'Weatherspoon', 'Gilpatrick', 'Funderburk', 'Mustard', 'Enterline', 'Redden', 'Hayford',
		'Hevey', 'Dey', 'Demaio', 'Chenard', 'Whalley', 'Light', 'Kleist', 'Huntsman', 'Drovin', 'Duenes', 'Weintraub',
		'Mcbeath', 'Harden', 'Streicher', 'Nadel', 'Philbrick', 'Ramm', 'Byrge', 'Broce', 'Olivera', 'Hamm', 'Tweedie',
		'Hershman', 'Hertzler', 'Fielding', 'Dao', 'Constante', 'Berens', 'Finks', 'Corvin', 'Lemmons', 'Cuenca' ];

	const id = uid();
	const firstName = firstNames[ Math.floor( Math.random() * firstNames.length ) ];
	const lastName = lastNames[ Math.floor( Math.random() * lastNames.length ) ];
	const name = firstName + ' ' + lastName;

	return { name, id };
}
