import uid from '@ckeditor/ckeditor5-utils/src/uid.js';

/* global location, window, document */

export function getDocIdFromUrl() {
	const docIdMatch = location.search.match( /docId=(.+)$/ );

	return docIdMatch ? decodeURIComponent( docIdMatch[ 1 ] ) : null;
}

export function generateUrlWithDocId( id ) {
	return `${ window.location.href.split( '?' )[ 0 ] }?docId=${ id }`;
}

export function updateDocIdInUrl( id ) {
	window.history.replaceState( {}, document.title, generateUrlWithDocId( id ) );
}

// Tries to get the document ID from a URL. When there is no ID in the URL, it adds one.
export function handleDocIdInUrl() {
	let id = getDocIdFromUrl();

	if ( !id ) {
		id = uid();
		updateDocIdInUrl( id );
	}

	return id;
}
